@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
html,body,#root{height:100%;margin:0;font-family:"Poppins", sans-serif;background-color:#e5e5e5}h2{margin:0;font-weight:bold;font-size:20px;line-height:30px;letter-spacing:0.374px;color:#262626}

.App{height:100%}.App__main{min-height:100%;display:-webkit-flex;display:flex;margin-left:210px}.App__accounts{padding-top:50px;padding-left:33px;padding-right:56px}.App__main-info{min-height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}

.sidebar{position:fixed;top:0;bottom:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;box-sizing:border-box;width:210px;padding:0 30px;border-right:1px solid #dadada;font-size:16px;line-height:24px;letter-spacing:0.374px}.sidebar__logo{width:-webkit-min-content;width:min-content;margin:40px 0 30px}.sidebar__list{margin:0;padding:0;list-style:none}.sidebar__item{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:18px 0;text-decoration:none;color:inherit;transition:all 0.3s}.sidebar__item img{transition:all 0.3s}.sidebar__item:hover{cursor:pointer;color:#FD3A6C}.sidebar__item:hover img{-webkit-filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%);filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%)}.sidebar__item--active{color:#FD3A6C}.sidebar__item--active img{-webkit-filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%);filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%)}.sidebar__item--active::before{content:"";height:5px;width:5px;margin-right:8px;border-radius:50%;background-color:#FD3A6C}.sidebar__link-title{margin-left:13px}.sidebar__profile{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;padding:18px 0;margin-bottom:18px}.sidebar__profile-text{margin-left:7px}

.accounts{-webkit-flex:1 1 auto;flex:1 1 auto;width:-webkit-min-content;width:min-content}.accounts__title{margin-bottom:20px}.accounts__list{padding:0;margin:0}.accounts__link{list-style:none}.accounts__link--active{display:-webkit-flex;display:flex}.accounts__link--active::before{content:"";width:10px;height:10px;margin-top:35px;-webkit-transform:translateY(-50%);transform:translateY(-50%);margin-right:20px;border-radius:50%;background-color:#FD3A6C}.accounts__account{margin-bottom:20px}.account{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;box-sizing:border-box;width:448px;height:70px;padding:0 27px;background:#fff;box-shadow:0px 0px 10px rgba(0,0,0,0.15);border:0;border-radius:20px;letter-spacing:0.374px;transition:all 0.3s}.account--active{background:linear-gradient(180deg, #FD3A6C 0%, #F5855C 100%)}.account:hover{cursor:pointer;background:linear-gradient(180deg, #FD3A6C 0%, #F5855C 100%);box-shadow:0px 0px 7px #EE4874}.account__currency{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-weight:500;font-size:15px;line-height:22px}.account__title{margin-left:7px}.account__balance{font-weight:bold;font-size:20px;line-height:30px}

.actions{width:100%;padding:26px 0 20px;border:1px solid #dadada;border-radius:40px 0 0 0}.action{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:100%;background-color:transparent;border:0;padding:20px 56px 20px 33px}.action:hover{cursor:pointer}.action__details{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.action__title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;margin-left:17px}

.details{-webkit-flex-grow:1;flex-grow:1;margin-top:50px;padding:33px 38px;background-color:#fff;border-radius:40px 0px 0px 0px}.details__text{margin:0;font-size:18px;line-height:27px;letter-spacing:0.374px;color:#b5b5b5}.details__balance{font-weight:bold;font-size:64px;line-height:96px;letter-spacing:0.374px;color:#262626}.details__account-balance{margin:10px 0 19px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.details__send-money{display:-webkit-flex;display:flex;grid-gap:20px;gap:20px;margin:28px 0 96px}.details__table{margin-bottom:26px}.details__table:last-child{margin-bottom:50px}.top-up{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;height:70px;padding:0 24px;box-sizing:border-box;background:#FFFFFF;border:1px solid #FD3A6C;border-radius:20px;cursor:pointer;text-decoration:none}.top-up__text{margin-left:10px;font-size:14px;line-height:120%;text-align:center;letter-spacing:0.374px;color:#FD3A6C}.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:110px;height:140px;padding:20px;box-sizing:border-box;background:#F8F8F8;border-radius:30px}.card--telegram{padding-top:35px;border:1px solid #FD3A6C;text-decoration:none}.card__text{margin-top:14px;font-size:14px;text-align:center;letter-spacing:0.374px;color:#262626}.card__text--telegram{color:#FD3A6C}.card__img{width:27px;-webkit-filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%);filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%)}.table{height:141px;border:1px solid #B5B5B5;box-sizing:border-box;border-radius:30px}.table__block{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:23px 27px}.table__block:first-child{border-bottom:1px solid #B5B5B5}.table__title{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.table__text{margin-left:11px;font-size:18px;line-height:24px;letter-spacing:0.374px;color:#B5B5B5}.table__text--special{margin-left:29px}.table__value{font-weight:500;font-size:18px;line-height:24px;letter-spacing:0.374px}.table__people{display:-webkit-flex;display:flex;grid-gap:8px;gap:8px}.table__people-more{height:24px;width:24px;border:1px solid #B5B5B5;border-radius:50%;text-align:center;font-weight:600;font-size:12px;line-height:24px;letter-spacing:0.374px;color:#B5B5B5}

.sendMoney{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:100%;text-decoration:none}.sendMoney__img{-webkit-filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%);filter:invert(37%) sepia(62%) saturate(4081%) hue-rotate(324deg) brightness(101%) contrast(98%)}.sendMoney__text{margin-left:14px;font-weight:bold;font-size:20px;line-height:30px;letter-spacing:0.374px;color:#262626}

